let walls;

export function maze(grid, startNode, finishNode) {
	const rows = range(grid.length);
	const columns = range(grid[0].length);
	walls = [];
	createBorder(grid, rows, columns);
	createMaze(grid, startNode, finishNode, rows, columns);
	return walls;
}

function range(length) {
	const result = [];
	for (let i = 0; i < length; ++i) {
		result.push(i);
	}
	return result;
}

function createBorder(grid, rows, columns) {
	rows.shift();
	for (const node of columns) {
		walls.push([0, node]);
	}
	for (const node of rows) {
		walls.push([node, 0]);
		walls.push([node, grid[0].length - 1]);
	}
	rows.pop();
	columns.shift();
	columns.pop();
	for (const node of columns) {
		walls.push([grid.length - 1, node]);
	}
}

function createMaze(grid, startNode, finishNode, rows, columns) {
	if (rows.length < 2 || columns.length < 2) {
		return;
	}
	let direction;
	let number;
	if (rows.length < columns.length) {
		direction = 0;
		number = getOddNumber(columns.length - 1);
		createWalls(
			startNode,
			finishNode,
			rows,
			columns,
			direction,
			columns[number]
		);
		createMaze(
			grid,
			startNode,
			finishNode,
			rows,
			columns.slice(0, columns.indexOf(columns[number]))
		);
		createMaze(
			grid,
			startNode,
			finishNode,
			rows,
			columns.slice(columns.indexOf(columns[number]) + 1)
		);
	} else {
		direction = 1;
		number = getOddNumber(rows.length - 1);
		createWalls(startNode, finishNode, rows, columns, direction, rows[number]);
		createMaze(
			grid,
			startNode,
			finishNode,
			rows.slice(0, rows.indexOf(rows[number])),
			columns
		);
		createMaze(
			grid,
			startNode,
			finishNode,
			rows.slice(rows.indexOf(rows[number]) + 1),
			columns
		);
	}
}

function getOddNumber(maximum) {
	let number = Math.floor(maximum / 2);
	if (number % 2 === 0) {
		if (number === maximum) {
			number -= 1;
		} else {
			number += 1;
		}
	}
	return number;
}

function getEvenNumber(maximum) {
	let number =
		Math.floor(Math.random() * (maximum / 2)) +
		Math.floor(Math.random() * (maximum / 2));
	if (number % 2 !== 0) {
		if (number === maximum) {
			number -= 1;
		} else {
			number += 1;
		}
	}
	return number;
}

function createWalls(startNode, finishNode, rows, columns, direction, number) {
	let isStartFinish = false;
	const tempWalls = [];
	if (direction === 0) {
		if (rows.length === 2) return;
		for (const temp of rows) {
			if (
				(temp === startNode.row && number === startNode.col) ||
				(temp === finishNode.row && number === finishNode.col)
			) {
				isStartFinish = true;
				continue;
			}
			tempWalls.push([temp, number]);
		}
	} else {
		if (columns.length === 2) return;
		for (const temp of columns) {
			if (
				(number === startNode.row && temp === startNode.col) ||
				(number === finishNode.row && temp === finishNode.col)
			) {
				isStartFinish = true;
				continue;
			}
			tempWalls.push([number, temp]);
		}
	}
	if (!isStartFinish) {
		// tempWalls.splice(getEvenNumber(tempWalls.length), 1);
	}
	for (const wall of tempWalls) {
		walls.push(wall);
	}
}
